#stories-section {
    background-color: var(--gold);
    color: black;
}

#stories-container {
    display: grid;
    gap: 1rem;
    max-width: 100%;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    justify-items: center;
}

.story-container {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;

    max-width: 40rem;
    width: 100%;
    gap: 1rem;
    padding: 0.5rem;

    background-color: var(--gold-alt);
    color: black;
    border-top: 3px solid var(--glass);
    border-radius: 1rem;
}
.story-container:hover {
    transform: scale(1.025);
    box-shadow: 0 0 1rem var(--gold-alt);
}

.profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 33%;
    max-width: 33%;

    background-color: white;
    border-radius: 0.5rem;
    padding-bottom: 0.5rem;
    box-shadow: 0 0 0.5rem var(--gold);
}

.story-portrait {
    width: 100%;
    height: auto;
}

.story-details {
    display: flex;
    flex-direction: column;
    text-align: start;
}

.story-name {
    font-size: 1.25rem;
    font-weight: 600;
}

.story-title {
    font-size: 1rem;
}

.quote {
    margin-top: 0.5rem;
    font-style: italic;
}

.story-achievements {
    margin-left: 2rem;
    margin-top: 1rem;
}

.story-container .icon {
    margin-top: 1rem;
    color: var(--crimson);
}
.story-container .icon:hover {
    color: white;
}

#stories-cta-btn {
    background-color: white;
    box-shadow: 0 0 2rem white;
    border: 3px solid var(--gold-alt);
}

@media (max-width: 768px) {
    #stories-container {
        grid-template-columns: 1fr; /* Single column on smaller screens */
    }
}